



@media screen and (min-width: 320px) {
  .all-categories-btn {
    width: 176px;
    height: 50px;
    cursor: pointer;
    color: rgba(5, 5, 5, .5);
    background-color: #fff;
    border: 1px solid rgba(5, 5, 5, .2);
    border-radius: 15px;
    margin-bottom: 24px;
    margin-left: 20px;
    padding: 14px 28px;
    font-family: Inter;
    font-size: 18px;
    font-weight: 500;
  }

  .btn.btn-active {
    color: #f8f8f8;
    background-color: #9bb537;
    border: 1px solid #9bb537;
  }

  .all-categories-list {
    width: 326px;
    height: 210px;
    flex-direction: column;
    align-items: flex-start;
    gap: 18px;
    display: flex;
    overflow-y: scroll;
  }

  .all-categories-item-btn:hover, :focus {
    color: #9bb537;
  }

  .all-categories-item-btn {
    color: rgba(5, 5, 5, .3);
    cursor: pointer;
    background: none;
    border: none;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.28571;
  }
}

@media screen and (min-width: 768px) {
  .all-categories-list {
    height: 416px;
    gap: 24px;
  }

  .all-categories-btn {
    margin-bottom: 30px;
  }

  .all-categiries-list-container {
    width: 176px;
    height: 416px;
  }
}

@media screen and (min-width: 1280px) {
  .all-categories-btn {
    margin-bottom: 42px;
  }

  .all-categiries-list-container {
    width: 235px;
    height: 416px;
  }
}





